@charset "UTF-8";

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

body {
    padding: 10px;
    display: flex;
    flex-direction: column;
}

.ul-main-class {
    display: flex;
    flex-direction: row;
    list-style: none;
    justify-content: center;
}

a {
    text-decoration: none;
    color: rgb(96, 3, 136);
    font-weight: bold;
}

.li-main-class {
    padding: 10px;
    margin-bottom: 10px;
}

.div-prompt-class {
    padding-top: 30px;
    text-align: center;
}